import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuerySales, VhAuth, VhQuery, VhImage } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {
  my_branch = this.router.getCurrentNavigation().extras.state;
  phone: any;
  display_promotion_selling_price: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').display_promotion_selling_price
  auto_select_lot : boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').auto_select_lot
  auto_hidden_lot: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').auto_hidden_lot
  warning_lot_expried : boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').warning_lot_expried
  management_product_manysize: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').management_product_manysize
  management_product_lot: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').management_product_lot
  auto_active_quantity_input: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').auto_active_quantity_input
  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    private vhImage: VhImage,
    public lang: LanguageService,
  ) {
  }
  ngOnInit(): void {
    this.initForm();
  }
  ngAfterViewInit() {
    this.phone = this.vhAlgorithm.vhphoneNumber(".phone-edit");
  }
  public branch: FormGroup;
 
  /**
   * gán các giá trị của chi nhánh nhận được vào form branch
   * @example   this.initForm();
   */
  initForm() {
    this.vhQuerySales.updateAppSettingNameBranch
    this.branch = new FormGroup({
      //email: new FormControl(this.my_branch['email']),
      name: new FormControl(this.my_branch['name'], [Validators.required]),
      phone: new FormControl(this.my_branch['phone'], [Validators.required, Validators.minLength(6)]),
      address: new FormControl(this.my_branch['address'], [Validators.required]),
      qr_ibanking: new FormControl(this.my_branch['qr_ibanking']),
    })
  }
  /**back */
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/branch']);
  }

  /**
   * show thông báo xác nhận xóa branch khỏi database
   *  @example   this.deleteItemConfirm();
   */
  deleteItemConfirm() {
    if (this.vhQuerySales.getDefaultBranch()._id == this.my_branch['_id']) {
      this.vhComponent.showToast(1500, this.lang.translate("Can't delete this branch"), "alert-toast");
      return;
    }
    if (this.vhQuerySales.getlocalBranchs().length == 1) {
      this.vhComponent.showToast(1500, this.lang.translate("Can't delete this branch"), "alert-toast");
      return;
    }
    if (this.vhAuth.checkMyPermission("branch_management_canceling_branch")) {
      this.vhComponent
        .alertConfirm(this.lang.translate("Confirm"), "",
          this.lang.translate("Are you sure to delete this branch") + " ?", "OK",
          this.lang.translate("Cancel"))
        .then(() => {
          if (this.my_branch['_id'] != this.vhQuerySales.getDefaultBranch()._id) {
            this.vhComponent.showLoading("").then(() => {
              this.vhQuerySales.deleteBranch(this.my_branch['_id']).then((res: any) => {
                this.vhComponent.hideLoading(0).then(() => {
                  if (res) this.vhComponent.showToast(1500, `${this.lang.translate("Branch")} ${this.branch.value.name} ${this.lang.translate("has been deleted successfully")}`, "success-toast");
                  else this.vhComponent.showToast(1500, this.lang.translate("Can't delete this branch"), "alert-toast")
                  setTimeout(() => {
                    this.goBack()
                  }, 300);
                });
              })
            })
          } else this.vhComponent.showToast(1500, this.lang.translate("Can't delete this branch"), "alert-toast")
        }, () => { })
        .catch(() => { })
    }
    else this.vhComponent.showToast(1500, this.lang.translate('You do not have this rights'), "alert-toast")

  }
  public updateBranch(value) {
    if (value.phone) value.phone = this.phone.getRawValue();
    if (this.branch.valid) this.vhQuerySales.updateBranch(this.my_branch['_id'], { ...value }).then(() => { }).catch(err => { })
  }

  /**
   * cập nhật lại trường cấu hình cho chi nhánh
   * @param value : trường cần update
   * @example this.updateAppSettingNameBranch({display_promotion_selling_price : true})
   */
  updateAppSettingNameBranch(value) {
    this.vhQuerySales.updateAppSettingNameBranch('permission_branch', value).then((bool) => {
      
    })
  }

  /**
    * mở giao diện chọn file để upload ảnh
    * @example getFile()
    */
  getFile() {
    document.getElementById("file-upload").click();
  }

  /** Lấy hình ảnh từ Desktop */
  onUpload(e?) {
    const file = e.target.files[0];
    this.vhImage.getImageFromDesktop(file, "branchs", this.branch.value['qr_ibanking'] || null,2000000).then(
      photoURL => {
        if (photoURL.vcode === 0) {
          this.branch.controls["qr_ibanking"].setValue(photoURL.data);
          this.updateBranch({qr_ibanking: photoURL.data})
          this.vhComponent.showToast(2000, (this.lang.translate("Image was loaded successfully")), 'success-toast');
        } else if (photoURL.vcode === 1) {
          this.vhComponent.showToast(2000, (this.lang.translate("Lỗi import file ảnh")), 'alert-toast');
        } else if (photoURL.vcode === 2) {
          this.vhComponent.showToast(2000, (this.lang.translate("Lỗi upload file ảnh")), 'alert-toast');

        } else if (photoURL.vcode === 3) {
          this.vhComponent.showToast(2000, (this.lang.translate("Lỗi không thể upload file ảnh lớn hơn 2MB")), 'alert-toast');

        }

      },
      () => {
        this.vhComponent.showToast(2000, (this.lang.translate("Image was failed")), 'alert-toast');
      }
    );
  }
}
